import * as React from 'react';
import styled from '@emotion/styled';

import Header from '@src/components/Header';
import { Router } from '@src/components/Router';
import { SpinningLoadingIcon } from '@src/ui/icons/LoadingIcon';
import { AppThemeProvider } from '@src/components/AppThemeProvider';
import * as errorReport from '@src/lib/errorReport';

import { ExtensionContext, useContextState } from '@src/context';

import { LoginPage } from '../login/Login';
import { useTheme } from '@emotion/react';
import * as userService from '@src/services/user';
import Browser from 'webextension-polyfill';
import { NoPermission } from '../noPermission';
import '@src/lib/heap';
import { abTester } from '@src/lib/abTester';
import { useReminderNotificationExperiment } from '@src/lib/hooks/useReminderNotificationExperiment';

const Popup: React.FC = () => {
  const [hasPermission, setHasPermission] = React.useState(true);
  const state = useContextState();
  const { refetch, data: user } = userService.useUser();

  // to update the setting when popup is opened
  const _ = useReminderNotificationExperiment();

  React.useEffect(() => {
    if (user) {
      if (window.heap) window.heap.identify(`${user.id}`);
      abTester.setAttributes({ id: user.id });
    }
  }, [user]);

  React.useEffect(() => {
    errorReport.init();

    if (!state.user) void refetch();

    if (window.browser)
      void Browser.permissions
        .contains({ origins: ['*://*.toggl.com/*', '*://toggl.com/*'] })
        .then(response => {
          setHasPermission(response);
        });
  }, []);

  return (
    <AppThemeProvider>
      <Root>
        {!hasPermission ? (
          <NoPermission />
        ) : state?.loading ? (
          <Loading />
        ) : !state?.user ? (
          <LoginPage />
        ) : (
          <ExtensionContext.Provider value={state}>
            <Header />
            <Router />
          </ExtensionContext.Provider>
        )}
      </Root>
    </AppThemeProvider>
  );
};

const Loading = () => {
  const theme = useTheme();

  return (
    <LoadingContainer>
      <SpinningLoadingIcon size={36} color={theme.colors.ui_50.regular} />
    </LoadingContainer>
  );
};

const Root = styled.div`
  display: flex;
  flex-direction: column;
  width: 470px;
`;

const LoadingContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 600px;
  background-color: ${({ theme }) => theme.colors.surface_primary.regular};
`;

export default Popup;
