import styled from '@emotion/styled';
import logo from '../../images/large-logo.svg';
import illustration from './illustration.svg';
import * as React from 'react';
import { defineMessages, FormattedMessage } from 'react-intl';
import Browser from 'webextension-polyfill';

const messages = defineMessages({
  title: {
    id: 'noPermission.title',
    defaultMessage: 'Please enable permissions',
  },
  description: {
    id: 'noPermission.description',
    defaultMessage: `To start using the extension, {br}
    click “Add Toggl Track” in the link below `,
  },
  cta: {
    id: 'noPermission.cta',
    defaultMessage: 'Enable permissions here',
  },
});

export const NoPermission: React.FC = () => {
  return (
    <Root>
      <img src={logo} alt='Toggl Track Logo' height={38} />
      <img src={illustration} alt='Clock with a flower' height={143} />
      <CopysContainer>
        <FormattedMessage {...messages.title} tagName={Title} />
        <FormattedMessage
          {...messages.description}
          tagName={Description}
          values={{ br: <br /> }}
        />
        <FormattedMessage {...messages.cta}>
          {value => (
            <Link
              onClick={() =>
                Browser.tabs.create({
                  url: Browser.runtime.getURL(
                    'src/pages/requestPermission/index.html'
                  ),
                })
              }
            >
              {value}
            </Link>
          )}
        </FormattedMessage>
      </CopysContainer>
    </Root>
  );
};

const Root = styled.div`
  display: flex;
  flex-direction: column;
  gap: 32px;
  align-items: center;
  padding: 122px 40px;
  background-color: ${({ theme }) => theme.colors.background.regular};
  color: ${({ theme }) => theme.colors.ui.regular};
  overflow: hidden;
`;

const CopysContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 16px;
`;

const Title = styled.h1`
  font-size: 20px;
  font-weight: 500;
  line-height: 160%;
`;

const Description = styled.span`
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
`;

const Link = styled.a`
  cursor: pointer;
  text-decoration: underline;
  color: ${({ theme }) => theme.colors.grape_120.regular};
  font-size: 14px;

  &:hover {
    color: ${({ theme }) => theme.colors.grape_120.hover};
  }
`;
