import * as React from 'react';
import styled from '@emotion/styled';

import * as Sentry from '@sentry/browser';
import Summary from '@src/components/Summary';
import TimeEntriesList from '@src/components/TimeEntriesList';
import { Timer } from '@src/components/timer/Timer';
import { groupTimeEntriesByDay } from '@src/lib/groupUtils';
import { calculateSums } from '@src/lib/timerUtils';
import * as projectService from '@src/services/project';

import { useExtensionContext } from '@src/context';
import useSetting from '@src/lib/hooks/useSetting';
import Pomodoro from '@src/components/Pomodoro';
import { TEsListOnboarding } from '@src/components/tesListOnboarding';
import { Db } from '@src/lib/db';
import { FormattedMessage, defineMessages } from 'react-intl';
import { Button } from '@src/ui/components';

const messages = defineMessages({
  surveyCTA: {
    id: 'home.surveyCTA',
    defaultMessage: 'Take a quick survey!',
  },
});

const Home: React.FC = () => {
  const { user, runningEntry, refetchEntries, timeEntries } =
    useExtensionContext();
  const { data: projectsById } = projectService.useProjects();
  const [pomodoroEnabled] = useSetting('pomodoroModeEnabled');
  const [pomodoroFocusEnabled] = useSetting('pomodoroFocusMode');
  const [pomodoroInterval] = useSetting('pomodoroInterval');
  const [onboardingStep, setOnboardingStep] = useSetting(
    'tesListOnboardingStep',
    true
  );
  const [surveyTaken, setSurveyTaken] = useSetting('surveyTaken');

  const { listEntries, projects } = React.useMemo(
    () =>
      timeEntries
        ? groupTimeEntriesByDay(Object.values(timeEntries), projectsById)
        : { listEntries: null, projects: null },
    [timeEntries, projectsById]
  );

  React.useEffect(() => {
    refetchEntries();
    Sentry.setUser({ id: String(user?.id) });
    Db.load('tesListOnboardingStep', 'initial');
  }, []);
  const TEsList = Object.values(timeEntries ?? {});

  return (
    <Root id='homeRoot'>
      {!!TEsList.length && <Timer />}
      {onboardingStep !== 'finished' && !!TEsList && (
        <TEsListOnboarding
          onboardingStep={onboardingStep}
          setOnboardingStep={setOnboardingStep}
          showImg={!TEsList.length || onboardingStep === 'initial'}
        />
      )}
      {onboardingStep !== 'initial' && (
        <>
          {!!listEntries?.length && user && (
            <Summary totals={calculateSums(TEsList, user.beginning_of_week)} />
          )}
          {pomodoroEnabled && pomodoroFocusEnabled && !!runningEntry ? (
            <Pomodoro entry={runningEntry} interval={pomodoroInterval} />
          ) : (
            timeEntries &&
            projects && (
              <TimeEntriesList timeEntries={listEntries} projects={projects} />
            )
          )}
        </>
      )}
      {!surveyTaken && !!listEntries?.length && (
        <SurveyButtonContainer>
          <Anchor
            href='https://toggl.typeform.com/to/sI2cCiTE'
            target='_blank'
            rel='noreferrer'
          >
            <StyledButton
              onClick={() => {
                setSurveyTaken(true);
              }}
            >
              <FormattedMessage {...messages.surveyCTA} />
            </StyledButton>
          </Anchor>
        </SurveyButtonContainer>
      )}
    </Root>
  );
};

const Root = styled.div`
  display: flex;
  flex-direction: column;
  background-color: ${({ theme }) => theme.colors.surface_secondary.regular};
  min-height: 600px;
`;

const SurveyButtonContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
`;

const Anchor = styled.a`
  width: 100%;
  display: flex;
  justify-content: center;
  text-decoration: none;
`;

const StyledButton = styled(Button)`
  width: fit-content;
  color: ${({ theme }) => theme.colors.accent.regular};
  border: none;
  font-weight: 500;
  text-decoration: none;
`;

export default Home;
