import * as React from 'react';
import moment from 'moment';
import styled from '@emotion/styled';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { defineMessages, FormattedMessage } from 'react-intl';
import { css, useTheme } from '@emotion/react';

import { ProjectsDropdown } from '@src/components/ProjectsDropdown';
import { TagsDropdown } from '@src/components/TagsDropdown';
import { MessageFrom } from '@src/index';
import { UnmetConstraintsTooltip } from '@src/components/UnmetConstraintsTooltip';
import { checkUnmetConstraints } from '@src/lib/utils';
import { Switch } from '@src/ui/components/Switch';
import {
  Button,
  DurationInput,
  FieldWrapper,
  PrimaryButton,
  SwitchLabel,
  SwitchWrapper,
  TextInput,
} from '@src/ui/components';
import {
  calculateStopDate,
  formatDuration,
  parseDuration,
} from '@src/lib/timerUtils';
import { useExtensionContext } from '@src/context';
import * as tagService from '@src/services/tag';
import * as projectsService from '@src/services/project';
import * as timeEntryService from '@src/services/timeEntry';
import * as errorReport from '@src/lib/errorReport';

import Dropdown from '@src/components/Dropdown';
import { DateTimeInput } from '@src/components/dateTimeInput/DateTimeInput';
import { TimeEntryV9 } from '@toggl/track-types';

const messages = defineMessages({
  doneButton: {
    id: 'editTimeEntry.doneButton',
    defaultMessage: 'Done',
  },
  cancelButton: {
    id: 'editTimeEntry.cancelButton',
    defaultMessage: 'Cancel',
  },
  deleteButton: {
    id: 'editTimeEntry.cancelButton',
    defaultMessage: 'Delete',
  },
  billableText: {
    id: 'editTimeEntry.billableText',
    defaultMessage: 'Billable',
  },
});

export const EditTimeEntry: React.FC<{
  submitOrCancelCB?: () => void;
  fromIntegration: MessageFrom;
  hideDurationInput?: boolean;
}> = props => {
  const theme = useTheme();
  const queryClient = useQueryClient();
  const { user: currentUser } = useExtensionContext();

  const {
    editingEntry,
    changeEditingEntry,
    changeView,
    timeEntriesConstraintsEnabled,
  } = useExtensionContext();
  const { mutate: updateEntry } = useMutation(
    ({
      entry,
      integration,
    }: {
      entry: TimeEntryV9;
      integration: MessageFrom;
    }) => timeEntryService.update(entry, integration),
    {
      onMutate: async ({ entry }) => {
        await timeEntryService.cancelQuery(queryClient);
        const previousEntries = timeEntryService.getQueryData(queryClient);
        timeEntryService.setQueryData(queryClient, old => ({
          ...old,
          [entry.id]: entry,
        }));
        return { previousEntries };
      },
      onError: (err, _newEntry, context) => {
        timeEntryService.setQueryData(queryClient, context?.previousEntries);
        errorReport.error(err as Error);
      },
      onSuccess: (entry, _, context) => {
        changeView('default');
        changeEditingEntry(undefined);
        if (props.submitOrCancelCB) props.submitOrCancelCB();
        timeEntryService.setQueryData(queryClient, {
          ...context?.previousEntries,
          [entry.id]: entry,
        });
      },
      onSettled: () => {
        setSubmiting(false);
      },
    }
  );

  const { mutate: deleteEntry } = useMutation(
    (entry: TimeEntryV9) => timeEntryService.remove(entry),
    {
      onMutate: async entry => {
        await timeEntryService.cancelQuery(queryClient);
        const previousEntries = timeEntryService.getQueryData(queryClient);
        timeEntryService.setQueryData(queryClient, old => {
          delete old?.[entry.id];
          return old;
        });
        return { previousEntries };
      },
      onError: (err, _, context) => {
        timeEntryService.setQueryData(queryClient, context?.previousEntries);
        errorReport.error(err as Error);
      },
      onSuccess: () => {
        changeView('default');
        changeEditingEntry(undefined);
        if (props.submitOrCancelCB) props.submitOrCancelCB();
      },
      onSettled: () => {
        setDeleting(false);
      },
    }
  );

  const { data: timeEntriesConstraints } =
    timeEntryService.useTimeEntriesConstraints(editingEntry?.workspace_id);

  const { data: projects } = projectsService.useProjects();
  const { data: tags } = tagService.useTags();

  const [submiting, setSubmiting] = React.useState(false);
  const [deleting, setDeleting] = React.useState(false);
  const doneRef = React.useRef(null);
  const [showUnmetTooltip, setShowUnmetTooltip] = React.useState(false);

  const [billable, setBillable] = React.useState(editingEntry?.billable);

  const [selectedTagsIds, setSelectedTagsIds] = React.useState(
    (editingEntry?.tag_ids ?? []) as number[]
  );

  const [selectedProjectId, setSelectedProjectId] = React.useState(
    editingEntry?.project_id
  );
  const [selectedTaskId, setSelectedTaskId] = React.useState(
    editingEntry?.task_id
  );

  const [duration, setDuration] = React.useState(
    formatDuration(editingEntry?.start as string, editingEntry?.stop as string)
  );

  const [description, setDescription] = React.useState(
    editingEntry?.description ?? ''
  );

  const tagsOptions = React.useMemo(() => {
    const wid =
      projects && selectedProjectId
        ? projects[selectedProjectId].workspace_id
        : currentUser?.default_workspace_id;
    return (
      (tags &&
        Object.values(tags)
          .filter(
            tag =>
              tag.workspace_id === wid ||
              selectedTagsIds.includes(Number(tag.id))
          )
          .sort((t1, t2) => t1?.name?.localeCompare(t2?.name))) ??
      []
    );
  }, [
    projects,
    selectedProjectId,
    currentUser?.default_workspace_id,
    tags,
    selectedTagsIds,
  ]);

  const updateDuration = (e: React.FocusEvent<HTMLInputElement, Element>) => {
    if (!editingEntry || editingEntry.duration < 0) return;

    changeEditingEntry({
      ...editingEntry,
      stop: calculateStopDate(
        editingEntry.start,
        parseDuration(e.target.value).toMillis() / 1000
      ),
      duration: parseDuration(e.target.value).toMillis() / 1000,
    });
  };

  const updateEditingTETimeFields = (start: string, stop: string) => {
    if (!editingEntry || editingEntry.duration < 0) return;

    const startValue = moment(start);
    const stopValue = moment(stop);

    const duration = stopValue.diff(startValue, 'seconds');

    changeEditingEntry({
      ...editingEntry,
      start: startValue.toISOString(),
      stop: stopValue.toISOString(),
      duration,
    });
  };

  React.useEffect(() => {
    setDuration(
      formatDuration(
        editingEntry?.start as string,
        editingEntry?.stop as string
      )
    );
  }, [editingEntry]);

  const missingProps = React.useMemo(
    () =>
      checkUnmetConstraints(timeEntriesConstraints, {
        description,
        project_id: selectedProjectId,
        task_id: selectedTaskId,
        tag_ids: selectedTagsIds,
      }),
    [
      timeEntriesConstraints,
      selectedTagsIds,
      selectedProjectId,
      selectedTaskId,
      description,
    ]
  );

  const handleTagsChange = (id: number) => {
    selectedTagsIds?.includes(id)
      ? setSelectedTagsIds(selectedTagsIds.filter(tagId => tagId !== id))
      : setSelectedTagsIds([id, ...selectedTagsIds]);
  };

  const handleSubmit = (e: React.MouseEvent) => {
    e.preventDefault();
    if (!editingEntry) return;
    setSubmiting(true);

    updateEntry({
      entry: {
        ...editingEntry,
        description,
        billable: !!billable,
        tag_ids: selectedTagsIds,
        project_id: selectedProjectId,
        task_id: selectedTaskId,
        start: editingEntry.start,
        stop: editingEntry.stop,
        duration: editingEntry.duration,
      },
      integration: props.fromIntegration,
    });
  };

  const handleCancel = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    changeView('default');

    if (props.submitOrCancelCB) props.submitOrCancelCB();
  };

  const handleDelete = (e: React.MouseEvent) => {
    e.preventDefault();
    if (!editingEntry) return;
    setDeleting(true);

    deleteEntry(editingEntry);
  };

  return (
    <Root id='homeRoot'>
      <FormWrapper>
        {props.fromIntegration.type === 'popup' && !props.hideDurationInput && (
          <FieldWrapper>
            <Dropdown onItemClick={e => null}>
              <Dropdown.Trigger>
                <DurationInput
                  css={css`
                    border: none;
                  `}
                  value={duration}
                  onChange={e => setDuration(e.target.value)}
                  onBlur={updateDuration}
                />
              </Dropdown.Trigger>
              <Dropdown.Overlay />
              <Dropdown.DateTimeContent>
                <DateTimeInput
                  start={editingEntry?.start}
                  stop={
                    editingEntry?.stop &&
                    moment(editingEntry.stop).format('HH:mm')
                  }
                  onDatesChange={updateEditingTETimeFields}
                />
              </Dropdown.DateTimeContent>
            </Dropdown>
          </FieldWrapper>
        )}
        <FieldWrapper>
          <TextInput
            type='text'
            placeholder='What are you doing?'
            value={description}
            onKeyDown={e => {
              e.stopPropagation();
            }}
            onChange={e => setDescription(e.target.value)}
          />
        </FieldWrapper>

        <ProjectsDropdown
          selectedProjectId={selectedProjectId}
          selectedTaskId={selectedTaskId}
          onChange={changes => {
            setSelectedProjectId(changes.projectId);
            setSelectedTaskId(changes.taskId);
            setBillable(changes.billable);
          }}
          maxHeight={276}
        />

        <TagsDropdown
          options={tagsOptions}
          selectedIds={selectedTagsIds}
          onChange={handleTagsChange}
          triggerText={
            selectedTagsIds.length
              ? selectedTagsIds.map(id => tags?.[id]?.name).join(', ')
              : 'Add tags'
          }
          maxHeight={238}
        />

        <FieldWrapper>
          <SwitchWrapper>
            <SwitchLabel>
              <FormattedMessage {...messages.billableText} />
            </SwitchLabel>
            <Switch
              onChange={() => setBillable(!billable)}
              value={!!billable}
            />
          </SwitchWrapper>
        </FieldWrapper>
        <FieldWrapper
          ref={doneRef}
          onMouseEnter={() => setShowUnmetTooltip(true)}
          onMouseLeave={() => setShowUnmetTooltip(false)}
        >
          <PrimaryButton
            disabled={missingProps.length > 0}
            loading={submiting}
            onClick={handleSubmit}
          >
            <FormattedMessage {...messages.doneButton} />
          </PrimaryButton>
          {timeEntriesConstraintsEnabled(editingEntry!.workspace_id) &&
          timeEntriesConstraints &&
          missingProps.length &&
          showUnmetTooltip ? (
            <UnmetConstraintsTooltip
              placement='bottom'
              anchorRef={doneRef}
              missingProps={missingProps}
              onClick={() => setShowUnmetTooltip(false)}
            />
          ) : undefined}
        </FieldWrapper>
        <ButtonsContainer>
          <Button onClick={handleCancel}>
            <FormattedMessage {...messages.cancelButton} />
          </Button>
          <Button
            loading={deleting}
            css={css`
              color: ${theme.colors.error.regular};
            `}
            onClick={handleDelete}
          >
            <FormattedMessage {...messages.deleteButton} />
          </Button>
        </ButtonsContainer>
      </FormWrapper>
    </Root>
  );
};

const Root = styled.div`
  background-color: ${({ theme }) => theme.colors.surface_primary.regular};
  margin-bottom: 32px;
`;

const FormWrapper = styled.div`
  display: flex;
  padding: 64px;
  height: 100vh;
  flex-direction: column;
`;

const ButtonsContainer = styled.div`
  display: flex;
  width: 100%;
  gap: 8px;
  justify-content: space-between;
`;
