import { Project, TimeEntryV9 } from '@toggl/track-types';
import { DateTime } from 'luxon';

const hasExistingGroup =
  (entry: TimeEntryV9) =>
  ([te]: TimeEntryV9[]) => {
    return (
      DateTime.fromISO(te.start).hasSame(
        DateTime.fromISO(entry.start),
        'day'
      ) &&
      te.description === entry.description &&
      te.project_id === entry.project_id &&
      te.task_id === entry.task_id &&
      te.workspace_id === entry.workspace_id &&
      (te.tags || []).join(',') === (entry.tags || []).join(',') &&
      te.billable === entry.billable
    );
  };

export const groupTimeEntriesByDay = (
  timeEntries: TimeEntryV9[],
  projectsById
) => {
  const { listEntries, projects } = [...timeEntries].reverse().reduce(
    (sum, entry) => {
      // Exclude running TE.
      if (entry.duration < 0) {
        return sum;
      }

      const existingGroupIndex = sum.listEntries.findIndex(
        hasExistingGroup(entry)
      );
      if (existingGroupIndex === -1) {
        // This TE group has not been seen yet.
        sum.listEntries.push([entry]);
      } else {
        // This TE group already exists.
        sum.listEntries[existingGroupIndex].push(entry);
        sum.listEntries[existingGroupIndex].sort((a, b) => {
          // Most recent entries first.
          if (a.start > b.start) return -1;
          if (b.start > a.start) return 1;
          return 0;
        });
      }

      const project = entry.project_id
        ? projectsById?.[entry.project_id]
        : null;
      if (project) sum.projects[project.id] = project;
      return sum;
    },
    { listEntries: [], projects: {} } as {
      listEntries: TimeEntryV9[][];
      projects: Record<string, Project>;
    }
  );

  return { listEntries, projects };
};
