import * as React from 'react';

import { useExtensionContext } from '@src/context';
import { EditTimeEntry } from '@src/pages/editTimeEntry/EditTimeEntry';
import Home from '@src/pages/home/Home';

export const Router: React.FC = () => {
  const { view } = useExtensionContext();

  return view === 'editEntry' ? (
    <EditTimeEntry fromIntegration={{ type: 'popup' }} />
  ) : (
    <Home />
  );
};
