const messages = defineMessages({
  emptyList: {
    id: 'timeEntriesList.emptyList',
    defaultMessage: 'Get ready to track time and boost your productivity!',
  },
});

const NO_DESCRIPTION = '(no description)';
const NO_PROJECT = '(no project)';

const groupDuration = (entries: TimeEntryV9[][]) => {
  const total = entries
    // Our minimum supported browser versions don't have Array.flat
    .reduce((acc, val) => acc.concat(val), [])
    .reduce((sum, { duration }) => sum + duration, 0);

  return formatDuration(DateTime.now().minus({ seconds: total }).toISO());
};

const getTimeEntryDayGroups = (
  timeEntries: Array<Array<TimeEntryV9>>
): { [date: string]: Array<Array<TimeEntryV9>> } => {
  return [...timeEntries]
    .sort((a, b) => {
      // Most recent entries first.
      if (a[0].start > b[0].start) return -1;
      if (b[0].start > a[0].start) return 1;
      return 0;
    })
    .filter(timeEntries => timeEntries.some(te => te.duration >= 0))
    .reduce(
      (groups: { [date: string]: Array<Array<TimeEntryV9>> }, entries) => {
        const date = DateTime.fromISO(entries[0].start).toFormat('yyyy-MM-dd');
        groups[date] = groups[date] || [];
        groups[date].push(entries);

        return groups;
      },
      {}
    );
};

interface TimeEntriesListProps {
  timeEntries: TimeEntryV9[][];
  projects: Record<string, Project>;
}
export default function TimeEntriesList(props: TimeEntriesListProps) {
  const { timeEntries = [], projects = {} } = props;
  const { runningEntry } = useExtensionContext();

  if (timeEntries.length === 0) {
    return (
      <Container>
        <div />
        {!runningEntry ? (
          <ClearerOnboarding />
        ) : (
          <NoEntries>
            <img src={emptyListImg} width='177px' height='179px' />
            <NoEntriesDescription>
              <FormattedMessage {...messages.emptyList} />
            </NoEntriesDescription>
          </NoEntries>
        )}
      </Container>
    );
  }

  const dayGroups = getTimeEntryDayGroups(timeEntries);

  return (
    <Container>
      <EntryList>
        {Object.entries(dayGroups).map(([date, groupEntries], idx) => (
          <EntryDayGroup key={idx}>
            <EntryHeading key={`tegroup-${idx}`}>
              <span>{DateTime.fromISO(date).toFormat('EEE, d MMM')}</span>
              <span>{groupDuration(groupEntries)}</span>
            </EntryHeading>
            {groupEntries.map((timeEntries, i) => {
              const project =
                (timeEntries[0].project_id &&
                  projects[timeEntries[0].project_id]) ||
                null;
              return (
                <TimeEntriesListItem
                  key={`te-${idx}-${i}`}
                  timeEntries={timeEntries}
                  project={project}
                />
              );
            })}
          </EntryDayGroup>
        ))}
      </EntryList>
    </Container>
  );
}

type TimeEntriesListItemProps = {
  timeEntries: Array<TimeEntryV9>;
  project: Project | null;
};

function TimeEntriesListItem({
  timeEntries,
  project,
}: TimeEntriesListItemProps) {
  const { changeEditingEntry, changeView } = useExtensionContext();
  const [isGroupCollapsed, setGroupCollapsed] = React.useState(true);
  const timeEntry = timeEntries[0];

  const totalDuration = timeEntries.reduce((sum, entry) => {
    if (entry.duration > 0) sum += entry.duration;
    return sum;
  }, 0);
  const entriesCount = timeEntries.length;
  const earliestStartTime = DateTime.fromISO(
    timeEntries[timeEntries.length - 1].start
  ).toFormat('HH:mm');

  const isGrouped = entriesCount > 1 && isGroupCollapsed;
  const toggleGrouping = React.useCallback(
    () => setGroupCollapsed(val => !val),
    [setGroupCollapsed]
  );

  const editEntry = (e: React.MouseEvent, timeEntry: TimeEntryV9) => {
    e.preventDefault();

    changeEditingEntry(timeEntry);
    changeView('editEntry');
  };

  const content = [
    entriesCount > 1 && (
      <TimeEntryRow
        renderHeader
        key={`grouped-${timeEntry.id}`}
        timeEntry={timeEntry}
        onRowClick={toggleGrouping}
        project={project}
        entriesCount={entriesCount}
        earliestStartTime={earliestStartTime}
        totalDuration={totalDuration}
      />
    ),
  ].concat(
    (!isGrouped ? timeEntries : []).map(timeEntry => (
      <TimeEntryRow
        renderHeader={false}
        key={timeEntry.id}
        timeEntry={timeEntry}
        onRowClick={e => editEntry(e, timeEntry)}
        project={project}
        indent={entriesCount > 1}
      />
    ))
  );

  return <>{content}</>;
}

function TimeEntryRow({
  timeEntry,
  onRowClick,
  project,
  renderHeader,
  entriesCount,
  earliestStartTime,
  totalDuration,
  indent,
}: {
  timeEntry: TimeEntryV9;
  onRowClick: (event: React.MouseEvent) => void;
  project: Project | null;
  renderHeader: boolean;
  entriesCount?: number;
  earliestStartTime?: string;
  totalDuration?: number;
  indent?: boolean;
}) {
  const queryClient = useQueryClient();
  const theme = useTheme();

  const { workspacePreferences } = useExtensionContext();

  const { mutate: startEntry } = useMutation(
    (entry: TimeEntryRequest) =>
      timeEntryService.start(entry, { type: 'popup' }),
    {
      onMutate: async entry => {
        await timeEntryService.cancelQuery(queryClient);
        const previousEntries = timeEntryService.getQueryData(queryClient);
        timeEntryService.setQueryData(queryClient, old => {
          const newEntries: Record<number, TimeEntryV9> = {};
          if (old) {
            Object.assign(newEntries, old);
          }
          const running = Object.values(newEntries).find(
            entry => entry.duration < 0
          );
          if (running) {
            const stopTime = new Date();
            const startTime = new Date(running.start);
            newEntries[running.id] = {
              ...running,
              stop: stopTime.toISOString(),
              duration: Math.floor(
                (stopTime.getTime() - startTime.getTime()) / 1000
              ),
            };
          }
          newEntries[-1] = { ...entry, id: -1 } as TimeEntryV9;
          return newEntries;
        });
        return { previousEntries };
      },
      onError: (err, _, context) => {
        timeEntryService.setQueryData(queryClient, context?.previousEntries);
        errorReport.error(err as Error);
      },
      onSuccess: (entry, _, context) => {
        const entries = {
          ...context?.previousEntries,
          [entry.id]: entry,
        };
        const running = Object.values(context?.previousEntries ?? {}).find(
          te => te.duration < 0
        );
        if (running) {
          const stopTime = new Date();
          const startTime = new Date(running.start);
          entries[running.id] = {
            ...running,
            stop: stopTime.toISOString(),
            duration: Math.floor(
              (stopTime.getTime() - startTime.getTime()) / 1000
            ),
          };
        }
        delete entries[-1];
        timeEntryService.setQueryData(queryClient, entries);

        track('start-time-entry', { context: 'History' });
      },
    }
  );
  const lockedDate = workspacePreferences?.report_locked_at;

  const isLocked =
    lockedDate &&
    DateTime.fromISO(timeEntry.start) < DateTime.fromISO(lockedDate);
  const description = timeEntry.description;
  const isBillable = !!timeEntry.billable;
  const tags =
    timeEntry.tags && timeEntry.tags.length > 0
      ? timeEntry.tags.join(', ')
      : '';

  const continueTimeEntry = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    const start = new Date();

    const entry = {
      at: start.toISOString(),
      billable: timeEntry.billable,
      description,
      duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
      start: start.toISOString(),
      tags: timeEntry.tags ?? [],
      tag_ids: timeEntry.tag_ids ?? [],
      task_id: timeEntry.task_id,
      project_id: timeEntry.project_id,
      workspace_id: timeEntry.workspace_id,
    } as unknown as TimeEntryRequest;

    startEntry(entry);
  };

  return (
    <EntryItem onClick={!isLocked ? onRowClick : undefined} indent={indent}>
      {isLocked ? <TimeEntryLockedIndicator /> : undefined}
      {renderHeader && entriesCount && earliestStartTime && (
        <GroupedEntryIcon>
          <GroupedEntryCounter
            title={`${entriesCount} entries since ${earliestStartTime}`}
          >
            {entriesCount}
          </GroupedEntryCounter>
        </GroupedEntryIcon>
      )}
      <EntryItemText>
        <TimeEntryDescription title={description && description}>
          {description || <Placeholder>{NO_DESCRIPTION}</Placeholder>}
        </TimeEntryDescription>
        <TimeEntryProject project={project} />
      </EntryItemText>
      <EntryItemActions>
        <TimeEntryDuration duration={totalDuration || timeEntry.duration} />
        <EntryIcons>
          {isBillable && <BillableIcon />}
          {tags && <TagsIcon title={tags} />}
          <StyledTimerButton
            theme={theme}
            onClick={continueTimeEntry}
            tabIndex={0}
          >
            <IconStart />
          </StyledTimerButton>
        </EntryIcons>
      </EntryItemActions>
    </EntryItem>
  );
}

